* {
  box-sizing: border-box;
}

h2, p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

html {
  height: 100vh;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  color: #fff;
  height: 100%;
  background-color: #1e5a1e;
  margin: 0;
  padding: 30px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.5;
  position: relative;
}

.btn-back {
  color: currentColor;
  gap: 10px;
  margin-bottom: 16px;
  margin-right: auto;
  text-decoration: none;
  transition: transform .25s;
  display: flex;
}

.btn-back:hover, .btn-back:focus {
  transform: translateX(-10px);
}

.btn-back span {
  width: 40px;
  height: 40px;
  background-color: #252121;
  background-image: url("icon-back.159d58f4.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 24px;
  border: 2px solid #fff;
  border-radius: 50%;
  display: flex;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px 20px;
}

.country-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.country-list__item {
  align-items: center;
  gap: 20px;
  display: flex;
}

.country-list__item:not(:last-child) {
  margin-bottom: 10px;
}

.country-info img {
  margin-bottom: 16px;
}

.country-info p {
  margin-bottom: 10px;
}

.country-info__title {
  margin-bottom: 16px;
}

